.nav {
  z-index: 1;
}

.nav--opened.nav {
  width: 100%;
}

.nav__toggle {
  position: relative;
  display: flex;
  align-items: center;
  z-index: 1;
  width: 23px;
  height: 22px;
  background-color: transparent;
  padding: 0;
  margin-right: 20px;
  cursor: pointer;
  border: none;
}
.nav__toggle::before, .nav__toggle::after {
  content: "";
  position: absolute;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  transition: all 0.3s ease 0s;
}
.nav__toggle::before {
  top: 0;
}
.nav__toggle::after {
  bottom: 0;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle:focus {
  opacity: 1;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:active {
  opacity: 0.3;
}

.nav__toggle-line {
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  transform: scale(1);
}

.nav__toggle--no-js.nav__toggle {
  display: none;
}

.nav--opened .nav__toggle {
  position: absolute;
  top: 17px;
  right: 0;
}
.nav--opened .nav__toggle::before {
  width: 27px;
  top: 45%;
  background-color: #ffffff;
  transform: rotate(-45deg) translate(0, -50%);
}
@media (min-width: 768px) {
  .nav--opened .nav__toggle {
    display: none;
  }
}

.nav--opened .nav__toggle::after {
  width: 27px;
  bottom: 50%;
  background-color: #ffffff;
  transform: rotate(45deg) translate(0, 50%);
}
@media (min-width: 768px) {
  .nav--opened .nav__toggle::after {
    display: none;
  }
}

.nav--opened .nav__toggle-line {
  transform: scale(0);
}

.nav__list {
  display: flex;
  flex-direction: column;
  justify-content: normal;
  align-items: normal;
  flex-wrap: wrap;
  background-color: #81b3d2;
  z-index: 1;
  text-align: center;
}
@media (min-width: 768px) {
