    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: normal;
  }
}
@media (min-width: 1200px) {
  .catalog__item-caption {
    position: absolute;
    width: 100%;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .catalog__item-caption--modified .catalog__description {
    display: flex;
    flex-direction: column;
    justify-content: normal;
    align-items: normal;
    gap: 2px;
  }
}
@media (min-width: 1200px) {
  .catalog__item-caption--modified {
    padding: 17px 20px 17px 25px;
  }
  .catalog__item-caption--modified .catalog__description {
    display: flex;
    flex-direction: column;
    justify-content: normal;
    align-items: normal;
    gap: 4px;
  }
}

.catalog__image {
  display: block;
  width: 100%;
}

.catalog__description {
  margin: 0 0 16px 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .catalog__description {
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .catalog__description {
    color: #ffffff;
  }
}

.catalog__author {
  display: block;
  font-weight: 400;
  line-height: 21px;
  text-transform: none;
  color: #333333;
}
@media (min-width: 1200px) {
  .catalog__author {
    color: #ffffff;
  }
}

.catalog__likes-quantity {
  position: relative;
  align-self: flex-start;
  border: none;
  cursor: pointer;
  background-color: transparent;
  color: #000000;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  text-transform: uppercase;
  padding: 2px 0 0 37px;
}
.catalog__likes-quantity::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 23px;
  height: 21px;
  mask-image: url("../images/sprite.svg#like");
  background-color: #000000;
  opacity: 0.3;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .catalog__likes-quantity::before {
    top: -4px;
  }
}
@media (min-width: 768px) {
  .catalog__likes-quantity {
    align-self: center;
    padding: 0 0 0 32px;
  }
}
@media (min-width: 1200px) {
  .catalog__likes-quantity {
    color: #ffffff;
  }
  .catalog__likes-quantity::before {
    background-color: #ffffff;
    opacity: 1;
  }
}
.catalog__likes-quantity:hover::before {
  background-color: #81b3d2;
  opacity: 1;
}
.catalog__likes-quantity:active {
  color: #81b3d2;
}
.catalog__likes-quantity:active::before {
  margin-top: -4px;
  transition: 0.1s ease-out;
}

.video-presentation {
  display: flex;
  flex-direction: column;
  justify-content: normal;
  align-items: normal;
  padding: 17px 20px 0 20px;
}
@media (min-width: 768px) {
  .video-presentation {
    padding: 54px 60px 40px 60px;
  }
}
@media (min-width: 1200px) {
  .video-presentation {
    padding: 60px 0 84px 0;
  }
}

.video-presentation__heading {
  padding: 0 20px 38px 20px;
}
@media (min-width: 768px) {
  .video-presentation__heading {
    padding: 0 100px 89px 100px;
  }
}
@media (min-width: 1200px) {
  .video-presentation__heading {
    padding: 0 378px 86px 378px;
  }
}

.video-presentation__title {
  margin: 0 0 28px 0;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .video-presentation__title {
    margin: 0 0 33px 0;
    font-size: 30px;
  }
}

.video-presentation__text {
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
}

.video-presentation__link {
  position: relative;
  align-self: center;
}
.video-presentation__link::before {
  content: "";
  position: absolute;
  width: 48px;
  height: 54px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  mask-image: url("../images/sprite.svg#play");
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
  transition: 0.3s linear;
}
.video-presentation__link:hover::before {
  width: 60px;
  height: 67px;
  transition: 0.3s linear;
}
.video-presentation__link:active::before {
  background-color: #81b3d2;
  width: 78px;
  height: 86px;
  opacity: 0.8;
}

.video-presentation__img {
  display: block;
}

.copyright {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
  position: relative;
  margin: 0 auto;
  max-width: 129px;
  min-height: 35px;
  padding: 0 41px 0 0;
}
@media (min-width: 768px) {
  .copyright {
    align-self: center;
    margin: 0;
    justify-self: end;
  }
}
.copyright::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 28px;
  height: 35px;
  mask-image: url("../images/sprite.svg#html-academy");
  background-color: #333333;
  background-repeat: no-repeat;
}
.copyright:hover {
  color: #81b3d2;
}
.copyright:hover::after {
  background-color: #81b3d2;
}
.copyright:active {
  color: #b3b3b3;
}
.copyright:active::after {
  background-color: #b3b3b3;
}

.copyright__link {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}

.modal {
  display: none;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.modal--success .modal__failure {
  display: none;
}
.modal--success .modal__success {
  display: block;
}

.modal__control {
  position: relative;
  background-color: #ffffff;
  box-shadow: 0 10px 0 rgba(0, 0, 0, 0.1);
  cursor: default;
}

.modal__failure {
  width: 320px;
  padding: 45px 41px 35px 41px;
  margin: auto;
  box-shadow: none;
}
@media (min-width: 768px) {
  .modal__failure {
    width: 400px;
    padding: 50px 47px;
  }
}

.modal__success {
  display: none;
  width: 320px;
  margin: auto;
  padding: 39px 20px 20px 20px;
  box-shadow: none;
  background: linear-gradient(to bottom, #ffffff 70%, #f2f2f2 70%);
}
@media (min-width: 768px) {
  .modal__success {
    width: 640px;
    padding: 53px 91px 47px 91px;
    background: linear-gradient(to bottom, #ffffff 58%, #f2f2f2 58%);
  }
}
@media (min-width: 768px) {
  .modal__success .modal__title {
    margin: 0 0 28px;
    line-height: 26px;
  }
}
@media (min-width: 768px) {
  .modal__success .modal__close-button {
    width: 100%;
  }
}

.modal__title {
  margin: 0 0 24px 0;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }
}

.modal__text {
  margin: 0 0 28px 0;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
}
@media (min-width: 768px) {
  .modal__text {
    margin: 0 0 47px;
  }
}
.modal__text--success {
  max-width: 244px;
  margin: 0 auto 69px auto;
}
@media (min-width: 768px) {
  .modal__text--success {
    max-width: 444px;
    margin: 0 auto 95px auto;
  }
}

.modal__close-button {
  position: relative;
  margin: 0 auto;
  display: block;
  min-width: 180px;
  padding: 17px 76px 15px 76px;
  text-align: center;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .modal__close-button {
    min-width: 272px;
    font-size: 21px;
  }
}

.not-found {
